import React from 'react';
import './card.styles.scss';
const Card = ({felicitaciones,lang}) => {

    

    const card = felicitaciones.palabras.map(f => ( 
                                            <>
                                                <img key={f.number}
                                                 src={f.imageUrl} alt="foto"/>  
                                                <p>
                                                    <span>{f.number}.-          </span>
                                                    
                                                    {lang === 'es' 
                                                    ? f.content.es
                                                    : f.content.en}
                                                </p>
                                            </>
                                         )//return html to render
                                    )//map 

    return (
        <div className="fotografia">
            {card}
        </div>
    )
}

export default Card;

